

.new{
    height: auto;
    margin-bottom: 50px;
}
.black-main{
	margin-top: 50px;
}
.card-deck{
	border: none!important;
}
.card{
    -webkit-box-shadow: none;
            box-shadow: none;
}
.list-group{
	-webkit-box-shadow: 1px 0.5px 6px #ccc;
	        box-shadow: 1px 0.5px 6px #ccc;
}
.list-group-item:hover{
    -webkit-box-shadow: 0 0.5px 10px #E0B431;
            box-shadow: 0 0.5px 10px #E0B431;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    border-radius: 5px;
}
.list-group-item p{
    font-size: 14px!important;
}


